import React from "react";
import {NavLink} from "react-router-dom";
import s from "./Header.module.css"

export const Header = (props: any) => {
    return (
        <div className={s.header}>
            <div className={s.header__img}>
                <img src="data:image/png;base64,"
                    alt="Нема"/></div>
            <div className={s.header__text}>До кінця 2022 року я буду працювати в IT сфері</div>
            <div>
                {props.isAuth ? <NavLink className={s.loginBlock} to={'/login'}>{props.login}</NavLink> :
                    <NavLink className={s.loginBlock} to={'/login'}>Login</NavLink>
                }
            </div>
        </div>
    )
}
export default Header;